#hero{
    width: 100%;
    height: fit-content;
    padding-top: 17rem;
    background: var(--primary-color-trans);
    height: fit-content;
}
.hero-wrapper{
    width: 100%;
    display: flex;
    gap: 30px;
    justify-content: center;
    align-items: stretch;
}
.hero-col{
    flex-basis: calc(50% - 15px);
    overflow: hidden;
}
.hero-left{
    width: 100%;
}
.hero-left .hero-text{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 2.2rem;
    padding-right: 3rem;
    position: relative;
    height: 100%;
    justify-content: center;
    padding-bottom: 5rem;
}
.hero-left .hero-text h1{
    color: var(--black-color);
    font-size: 5.3rem;
    font-weight: 600;
}
.hero-left .hero-text p{
    font-weight: 400;
    padding-right: 3rem;
}
.hero-left .hero-btn{
    display: flex;
    flex-wrap: wrap;
    gap: 3rem;
    margin-top: 1rem;
}
.hero-right{
    text-align: center;
}
.hero-right img{
    width: 100%;
    margin-bottom: -3px;
}
@media(max-width: 800px){
    .hero-wrapper{
        flex-direction: column-reverse;
    }
    .hero-right img{
        max-width: 600px;
        margin: 0;
    }
    .hero-left .hero-text{
        align-items: center;
        text-align: center;
        padding: 3rem 0 5rem 0;
    }
    .hero-left .hero-text p{
        max-width: 550px;
    }
    .hero-left .hero-btn{
        justify-content: center;
    }
}