@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}
::-webkit-scrollbar{
  width: 12px;
}
::-webkit-scrollbar-thumb{
  background: var(--primary-color);
}
::-webkit-scrollbar-track{
  background: #48c89b65;
}

body {
  background: var(--white-color);
  overflow-x: hidden;
  font-family: var(--primary-font);
  position: relative;
}

::selection {
  background: var(--primary-color);
  color: var(--white-color);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 25px;
}

.btn {
  width: fit-content;
  height: fit-content;
  cursor: pointer;
  transition: var(--primary-transition);
  padding: 15px 25px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
}

.btn-primary {
  color: var(--white-color);
  background: var(--primary-color);
  border: 2px solid transparent;
}

.btn-primary:hover {
  color: var(--primary-color);
  background: transparent;
  border: 2px solid var(--primary-color);
}

.btn-secondary {
  color: var(--primary-color);
  background: transparent;
  border: 2px solid var(--primary-color);
}

.btn-secondary:hover {
  color: var(--white-color);
  background: var(--primary-color);
  border: 2px solid transparent;
}

p {
  font-size: 16px;
  color: var(--gray-color);
  font-weight: 300;
}

.section-badge {
  font-size: 18px;
  font-weight: 500;
  color: var(--secondary-color);
}

.section-header {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 1.5rem;
}

.section-title {
  font-size: 4.2rem;
  font-weight: 600;
}

.title-2 {
  font-size: 22px;
  font-weight: 500;
  color: var(--black-color);
}

:root {
  --primary-color: #48C89C;
  --secondary-color: #FF9442;
  --primary-color-trans: #F4FAF7;
  --primary-box-shadow: 5px 15px 25px 0px rgba(139.00000000000003, 173, 138, 0.2);
  --white-color: #fff;
  --black-color: #202430;
  --gray-color: #646464;
  --primary-transition: 0.3s ease-in-out;
  --primary-font: 'Rubik', sans-serif;
}

@media(max-width: 1050px) {
  html {
    font-size: 57%;
  }
}

@media(max-width: 950px) {
  html {
    font-size: 52%;
  }
}

@media(max-width: 850px) {
  html {
    font-size: 47%;
  }
}

@media(max-width: 750px) {
  html {
    font-size: 42%;
  }

  .btn {
    padding: 13px 20px;
    border-radius: 7px;
    font-size: 13px;
  }

  p {
    font-size: 15px;
  }

  .section-badge {
    font-size: 16px;
  }
}

@media(max-width: 430px) {
  p {
    font-size: 14px;
  }
}

@media(max-width: 350px) {
  html {
    font-size: 38%;
  }

  .title-2 {
    font-size: 20px;
  }
}