header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    height: fit-content;
    background: transparent;
    padding: 3rem 0;
    transition: var(--primary-transition);
    z-index: 999;
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
}

header.active {
    padding: 2rem 0;
    box-shadow: 0 0 2px 0 #ddd;
}

.header-wrapper {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 0 20px;
}

.h-logo {
    font-size: 45px;
    font-weight: 600;
    color: var(--secondary-color);
}

.nav-bar {
    display: flex;
    align-items: center;
}

.main-menu {
    display: flex;
    gap: 50px;
    margin-top: 15px;
}

.main-menu>li {
    width: fit-content;
    height: fit-content;
}

.main-menu>li>a {
    color: var(--black-color);
    font-size: 17px;
    font-weight: 500;
    transition: var(--primary-transition);
}

.main-menu>li>a.active,
.main-menu>li:hover>a {
    color: var(--primary-color);
}

.h-contact-btn {
    width: fit-content;
    height: fit-content;
    cursor: pointer;
    transition: var(--primary-transition);
    padding: 15px 25px;
    border-radius: 8px;
    font-size: 14px;
    font-weight: 500;
    color: var(--white-color) !important;
    background: var(--primary-color) !important;
    border: 2px solid transparent !important;
}

.h-contact-btn:hover,
.h-contact-btn.active {
    color: var(--primary-color) !important;
    background: transparent !important;
    border: 2px solid var(--primary-color) !important;
}

.sub-menu {
    font-size: 17px;
    font-weight: 500;
    position: relative;
    padding-bottom: 15px;
}

.sub-menu>span {
    transition: var(--primary-transition);
    cursor: pointer;
}

.sub-menu:hover>span {
    color: var(--primary-color);
}

.sub-menu>ul {
    position: absolute;
    top: 100%;
    left: 0;
    display: flex;
    flex-direction: column;
    width: 170px;
    transform: scaleY(0);
    transition: var(--primary-transition);
    transform-origin: top;
}

.sub-menu.active>ul {
    transform: scaleY(1);
}

.sub-menu>ul>li {
    flex-basis: 100%;
    display: flex;
    transition: var(--primary-transition);
}

.sub-menu>ul>li>a {
    flex-basis: 100%;
    padding: 0 30px;
    line-height: 3;
    color: var(--white-color);
    border-bottom: 1px solid var(--gray-color);
    transition: var(--primary-transition);
    background: var(--black-color);
}

.sub-menu>ul>li>a:hover,
.sub-menu>ul>li>a.active {
    background: var(--primary-color);
}

.sub-menu>ul>li:last-child>a {
    border: none;
}

.nav-toggler {
    width: fit-content;
    height: fit-content;
    cursor: pointer;
    display: none;
}

.nav-toggler>div {
    width: 25px;
    height: 2px;
    background-color: var(--primary-color);
    margin-top: 5px;
    transition: var(--primary-transition);
}

.nav-toggler>.line1 {
    margin: 0;
}

.nav-toggler.active>.line1 {
    transform: rotate(-45deg) translate(-5px, 5px);
}

.nav-toggler.active>.line2 {
    opacity: 0;
}

.nav-toggler.active>.line3 {
    transform: rotate(43deg) translate(-5px, -5px);
}

@media(max-width: 800px) {
    .main-menu {
        gap: 40px;
    }
}

@media(max-width: 750px) {
    .nav-toggler {
        display: block;
    }

    .main-menu {
        position: fixed;
        top: 75px;
        right: -250px;
        height: calc(100vh - 75px);
        width: 250px;
        flex-direction: column;
        background: var(--primary-color);
        align-items: center;
        transition: var(--primary-transition);
        padding-top: 40px;
        gap: 30px;
        opacity: 0;
    }

    .nav-bar.active>.main-menu {
        right: 0;
        opacity: 1;
    }
    .main-menu li a,
    .sub-menu ul li a{
        font-size: 15px;
    }

    .main-menu.sticky {
        top: 65px;
    }

    .main-menu>li>a.active,
    .main-menu>li:hover>a {
        color: var(--white-color);
    }

    .h-contact-btn {
        color: var(--primary-color) !important;
        background: var(--black-color) !important;
        padding: 13px 20px;
        border-radius: 7px;
        font-size: 14px !important;
    }

    .h-contact-btn:hover,
    .h-contact-btn.active {
        color: var(--black-color) !important;
        border: 2px solid var(--black-color) !important;
    }

    .sub-menu:hover>span {
        color: var(--white-color);
    }

    .sub-menu {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;
        margin-bottom: -20px;
    }

    .sub-menu>ul {
        position: unset;
        height: 0 !important;
    }

    .sub-menu.active>ul {
        height: fit-content !important;
    }

    .sub-menu>ul>li>a {
        color: var(--white-color);
    }

    .sub-menu>ul>li>a:hover,
    .sub-menu>ul>li>a.active {
        background: var(--white-color);
        color: var(--black-color);
    }
}
@media(max-width: 430px){
    .h-logo {
        font-size: 35px;
        margin: 7px 0;
    }
}