#hp-services{
    padding: 11rem 0;
}
.hp-service-wrapper{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 5rem;
}
.hp-service-wrapper .section-header h2{
    max-width: 90rem;
}
.hp-services-container{
    width: 100%;
    display: flex;
    gap: 4rem;
    justify-content: space-between;
    flex-wrap: wrap;
}
.hp-service-card{
    flex-basis: calc(50% - 2rem);
    display: flex;
    flex-direction: column;
    gap: 2rem;
    background: var(--white-color);
    border-radius: 1.5rem;
    padding: 3rem;
}
.hp-service-card:nth-child(1),
.hp-service-card:nth-child(4){
    box-shadow: var(--primary-box-shadow);
}
.hp-service-card img{
    max-width: 14rem;
}
@media(max-width: 700px){
    .hp-service-card{
        flex-basis: 100%;
    }
}