#abAbout{
    padding: 14rem 0;
}
.abAbout-wrapper{
    width: 100%;
    display: flex;
    gap: 8rem 6rem;
    align-items: center;
}
.abAbout-left{
    flex-basis: calc(40% - 3rem);
    overflow: hidden;
}
.abAbout-left img{
    width: 100%;
    border-radius: 10px;
}
.abAbout-right{
    flex-basis: calc(60% - 3rem);
}
.abAbout-right .text{
    display: flex;
    flex-direction: column;
    gap: 3rem;
}
.abAbout-right .text img{
    width: 14rem;
    margin-bottom: -2rem;
    margin-top: 1.5rem;
}
.abAbout-right .text .sign-title{
    font-size: 16px;
    font-weight: 500;
    color: var(--black-color);
}
@media(max-width: 740px){
    .abAbout-wrapper{
        flex-wrap: wrap;
    }
    .abAbout-left{
        text-align: center;
    }
    .abAbout-left img{
        max-width: 500px;
    }
    .abAbout-left,
    .abAbout-right{
        flex-basis: 100%;
    }
    .abAbout-right{
        text-align: center;
    }
    .abAbout-right .text img{
        align-self: center;
    }
}