footer{
    background: var(--black-color);
}
.footer-wrapper{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 6rem 0 4rem; 
}
.footer-top{
    width: 100%;
    display: flex;
    gap: 50px 30px;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    padding-bottom: 4rem;
    border-bottom: 2px solid #2B3347;
}
.footer-col{
    flex-basis: calc(25% - 25px);
    display: flex;
    flex-direction: column;
    gap: 25px;
}
.footer-col>h3,
.footer-col>h3>a{
    color: var(--white-color);
    font-size: 21px;
    font-weight: 400;
}
.footer-col>.f-logo{
    font-size: 45px;
    font-weight: 600;
    color: var(--secondary-color);
}
.footer-col>p{
    color: var(--white-color);
    opacity: 0.9;
}
.footer-col>ul{
    display: flex;
    flex-direction: column;
    gap: 15px;
}
.footer-col>ul>li>a{
    font-size: 16px;
    color: var(--white-color);
    opacity: 0.9;
    position: relative;
    width: fit-content;
    height: fit-content;
    transition: var(--primary-transition);
}
.footer-col>ul>li>a:hover{
    color: var(--primary-color);
    opacity: 1;
}
.footer-col>ul>li>a::after{
    position: absolute;
    content: "";
    width: 100%;
    height: 2px;
    background: var(--secondary-color);
    top: 100%;
    left: 0;
    transform: scaleX(0);
    transform-origin: left;
    transition: var(--primary-transition);
}
.footer-col>ul>li>a:hover::after{
    transform: scaleX(1);
}
.footer-col>.f-social{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    align-items: center;
    gap: 20px;
}
.footer-col>.f-social>a{
    width: fit-content;
    height: fit-content;
    transition: var(--primary-transition);
    color: var(--white-color);
    font-size: 20px;
}
.footer-col>.f-social>a:hover{
    color: var(--secondary-color);
}
.footer-bottom{
    width: 100%;
    text-align: center;
    padding-top: 4rem;
}
.footer-bottom>p{
    color: var(--white-color);
}
@media(max-width: 920px){
    .footer-col{
        flex-basis: calc(50% - 25px);
        gap: 20px;
    }
}
@media(max-width: 500px){
    .footer-col{
        flex-basis: 100%;
        gap: 15px;
    }
    .f-logo{
        font-size: 36px !important;
    }
}