#testimonial{
    padding: 10rem 0;
}
.testimonials{
    padding-top: 5rem;
}
.testi-slider{
    padding: 2rem 4rem 40px 4rem;
    position: relative;
}
.swiper {
    width: 100%;
    height: 100%;
}
.testi-slide{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}
.testi-slide h2{
    font-size: 22px;
    font-weight: 500;
    color: var(--black-color);
}
.testi-slide h3{
    color: var(--gray-color);
    font-weight: 400;
    font-size: 16px;
    padding-top: 5px;
}
.testi-stars{
    display: flex;
    gap: 5px;
    align-items: center;
    padding: 2rem 0 2.5rem 0;
}
.testi-stars i{
    font-size: 20px;
    color: var(--secondary-color);
}
.testi-slide p{
    font-weight: 400;
    opacity: 0.8;
}
.testi-slider .swiper-pagination span{
    width: 12px !important;
    height: 12px !important;
    transition: var(--primary-transition);
}
.testi-slider .swiper-pagination span.swiper-pagination-bullet-active{
    background: var(--primary-color);
}
@media(max-width: 425px){
    .testi-slide h2{
        font-size: 19px;
    }
    .testi-stars{
        gap: 4px;
    }
    .testi-stars i{
        font-size: 18px;
    }
    .testi-slider{
        padding-left: 0;
        padding-right: 0;
    }
    .testi-slider .swiper-pagination span{
        width: 10px !important;
        height: 10px !important;
    }
}