#hp-working-step {
    width: 100%;
    padding: 10rem 0;
    background: var(--primary-color-trans);
}

.hp-working-step-wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 7rem 0;
}

.hp-ws-phrase-container {
    width: 100%;
    display: flex;
    gap: 7rem 30px;
}

.hp-ws-phrase-container .left,
.hp-ws-phrase-container .right {
    flex-basis: calc(50% - 15px);
    overflow: hidden;
}

.hp-ws-phrase-wrapper {
    display: flex;
    flex-direction: column;
    gap: 5rem;
}

.hp-ws-phrase {
    width: 100%;
    display: flex;
    gap: 2rem;
    align-items: flex-start;
}

.hp-ws-phrase span {
    font-size: 10rem;
    font-weight: 700;
    color: var(--primary-color);
    line-height: 8rem;
}

.hp-ws-phrase:nth-child(1) span {
    opacity: calc(100% / 4);
}

.hp-ws-phrase:nth-child(2) span {
    opacity: calc(100% / 2);
}

.hp-ws-phrase div {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.hp-ws-phrase div h3 {
    font-size: 23px;
    font-weight: 500;
}

.hp-ws-phrase-img {
    text-align: center;
}

.hp-ws-phrase-img img {
    width: calc(100% - 30px);
    position: relative;
    animation: hp-ws-phrase-img 5s linear infinite;
}
@keyframes hp-ws-phrase-img{
    0%{
        transform: translateY(0);
    }
    50%{
        transform: translateY(-10px);
    }
}

@media(max-width: 768px) {
    .hp-ws-phrase-container {
        flex-wrap: wrap-reverse;
    }

    .hp-ws-phrase-container .left,
    .hp-ws-phrase-container .right {
        flex-basis: 100%;
    }
}
@media(max-width: 600px){
    .hp-ws-phrase div h3 {
        font-size: 20px;
    }
}