#hp-about {
    padding: 10rem 0;
}

.hp-about-wrapper {
    width: 100%;
    display: flex;
    gap: 5rem 6rem;
    justify-content: center;
}

.hp-about-wrapper .section-header,
.hp-about-wrapper .hp-about-cards {
    flex-basis: calc(50% - 3rem);
}

.hp-about-wrapper .section-header {
    align-items: flex-start;
    text-align: start;
}

.hp-about-wrapper .section-header p {
    max-width: 47rem;
    font-weight: 400;
    opacity: 0.7;
    margin-bottom: 1.5rem;
}

.hp-about-cards {
    display: flex;
    flex-wrap: wrap;
    gap: 4rem;
    justify-content: center;
}

.hp-about-card {
    flex-basis: calc(50% - 2rem);
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.hp-about-card div {
    display: flex;
    gap: 2.5rem;
    align-items: flex-start;
    font-size: 2.3rem;
    font-weight: 500;
    color: var(--black-color);
}

.hp-about-card p {
    font-weight: 400;
    opacity: 0.7;
}


.hp-about-card div i {
    font-size: 28px;
    min-width: 55px;
    min-height: 55px;
    width: 55px;
    height: 55px;
    background: var(--primary-color);
    color: var(--white-color);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    transition: var(--primary-transition);
}

.hp-about-card:hover div i {
    color: var(--black-color);
}

@media(max-width: 890px) {
    .hp-about-wrapper{
        flex-wrap: wrap;
        gap: 8rem;
    }
    .hp-about-wrapper .section-header,
    .hp-about-wrapper .hp-about-cards {
        flex-basis: 100%;
    }
    .hp-about-wrapper .section-header p{
        max-width: 58rem;
    }

    .hp-about-wrapper .section-header {
        align-items: center;
        text-align: center;
    }

    .hp-about-card div {
        font-size: 22px;
    }
}
@media(max-width: 470px){
    .hp-about-card div {
        font-size: 20px;
    }
    .hp-about-card div i {
        font-size: 25px;
        min-width: 50px;
        min-height: 50px;
        width: 50px;
        height: 50px;
    }
    .hp-about-card{
        flex-basis: 100%;
    }
    .hp-about-cards{
        gap: 7rem;
    }
}