#banner{
    background: var(--black-color);
    padding: 10rem 0;
}
.banner-wrapper{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 2rem;
}
.banner-badge{
    font-size: 14px;
    color: var(--secondary-color);
}
.banner-title{
    font-size: 4rem;
    color: var(--white-color);
    font-weight: 600;
}