.g-social-media-links{
    display: flex;
    flex-direction: column;
    gap: 25px;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 50%;
    right: 30px;
    transform: translateY(-50%);
    z-index: 888;
}
.g-social-media-links>a{
    width: 40px;
    height: 40px;
    border-radius: 100%;
    background: var(--secondary-color);
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 22px;
    color: var(--white-color);
    transition: var(--primary-transition);
}
.g-social-media-links>a:hover{
    background: var(--primary-color);
}
@media(max-width: 768px){
    .g-social-media-links>a{
        width: 30px;
        height: 30px;
        font-size: 17px;
    }
    .g-social-media-links{
        gap: 20px;
        right: 25px;
    }
}