#hp-articles{
    padding: 10rem 0;
}
.hp-articles-wrapper{
    display: flex;
    flex-direction: column;
    gap: 5rem;
}
.hp-articles-cards{
    display: flex;
    gap: 40px 30px;
    justify-content: center;
}
.hp-article-card{
    flex-basis: calc((100% / 3) - ((30px * 2) / 3));
    overflow: hidden;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}
.hp-article-card .img{
    width: 100%;
    height: 25rem;
    overflow: hidden;
    position: relative;
}
.hp-article-card .img::after{
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: #ffffff44;
    transition: var(--primary-transition);
    opacity: 0;
}
.hp-article-card:hover .img::after{
    opacity: 1;
}
.hp-article-card .img img{
    width: 100%;
    height: 100%;
    transition: var(--primary-transition);
}
.hp-article-card:hover .img img{
    transform: rotate(3deg) scale(1.05);
}
.hp-article-card h3{
    font-size: 22px;
    font-weight: 500;
    color: var(--black-color);
}
.hp-article-card .author-text{
    display: flex;
    gap: 15px;
    align-items: center;
}
.hp-article-card .author-text span{
    font-size: 15px;
    color: var(--black-color);
    opacity: 0.7;
    display: flex;
    align-items: center;
    gap: 5px;
}
.hp-see-all-blogs{
    align-self: center;
    margin-top: 3rem;
}
@media(max-width: 960px){
    .hp-article-card h3{
        font-size: 20px;
    }
}
@media(max-width: 850px){
    .hp-article-card h3{
        font-size: 18px;
    }
}
@media(max-width: 768px){
    .hp-articles-cards{
        flex-wrap: wrap;
    }
    .hp-article-card{
        flex-basis: calc(50% - 15px);
    }
}
@media(max-width: 550px){
    .hp-article-card{
        flex-basis: 100%;
    }
}